import { Component, OnInit } from '@angular/core';
import { C3Service } from 'src/app/_services/c3.service';
import { FileFolder, FileDevice } from 'src/app/_models';
import { MatDialog } from '@angular/material';
import { AddFromCompasDriveDialogComponent } from '../add-from-compas-drive-dialog/add-from-compas-drive-dialog.component';
import { ShareFileService } from 'src/app/_services/share-file.service';
import { TreeviewItem } from 'ngx-treeview';
import { timestamp } from 'rxjs/operators';


@Component({
  selector: 'app-pss-config',
  templateUrl: './pss-config.component.html',
  styleUrls: ['./pss-config.component.scss']
})
export class PssConfigComponent implements OnInit {

  total: FileDevice;
  drawings: FileFolder = null;
  filesFromDevice: FileList;
  public inProgress: boolean;

  constructor(
    private c3Services: C3Service,
    public dialog: MatDialog,
    private shareFileService: ShareFileService) { }

  ngOnInit() {
    this.resetTotal();
    this.c3Services.getPssDocument().subscribe(data => {
      this.drawings = data;
      this.drawings.files = this.drawings.files.filter(item => { return item.title !== 'Proposal16102019.png' });
      this.drawings.files.forEach(item => {
        item.device = new FileDevice();
      })
    });
  }

  updateTotal() {
    this.resetTotal();
    this.drawings.files.forEach(item => {
      this.total.mv_breaker += item.device.mv_breaker;
      this.total.mv_fuses += item.device.mv_fuses;
      this.total.mv_trans += item.device.mv_trans;
      this.total.mv_motor += item.device.mv_motor;
      this.total.mv_cabs += item.device.mv_cabs;
      this.total.lv_sw_fuse += item.device.lv_sw_fuse;
      this.total.lv_adj_brk += item.device.lv_adj_brk;
      this.total.lv_non_adj_brk += item.device.lv_non_adj_brk;
      this.total.lv_trans += item.device.lv_trans;
      this.total.lv_pan_100 += item.device.lv_pan_100;
      this.total.lv_pan_50 += item.device.lv_pan_50;
      this.total.lv_cp += item.device.lv_cp;
      this.total.genset += item.device.genset;
    })
  }

  resetTotal() {
    this.total = {
      mv_breaker: 0,
      mv_fuses: 0,
      mv_trans: 0,
      mv_motor: 0,
      mv_cabs: 0,
      lv_sw_fuse: 0,
      lv_adj_brk: 0,
      lv_non_adj_brk: 0,
      lv_trans: 0,
      lv_pan_100: 0,
      lv_pan_50: 0,
      lv_cp: 0,
      genset: 0
    }
  }

  openAddFromCompasDriveDialog() {
    // console.log('Add from compas drive called!!');
    const dialogRef = this.dialog.open(AddFromCompasDriveDialogComponent, {
      width: `500px`,
      data: {}
    });
    dialogRef.afterClosed().subscribe(data => {

      if (data.length) {
        data.map(item => {
          if (item.checked === true) {
            item.device = new FileDevice();
            this.drawings.files.push(item);
          }
        });
      }

    });
  }

  fileChange($event: any) {
    console.log('file events', $event);
    // const user = localStorage.getItem('userId');
    this.filesFromDevice = $event.target.files;
    console.log($event.target.files);
    Array.from(this.filesFromDevice).forEach(file => {
      console.log(file);
      this.shareFileService.addFile(new TreeviewItem({ text: file.name, value: 1 }));
      console.log(file);
      this.c3Services.uploadFile(`Global/PSS Documents`, file, null, 'comment').subscribe(data => {
        // console.log('data inside filechange', data);
        // console.log('file inside filechange', file);
        if (typeof data === 'number') {
          this.inProgress = true;
          // console.log(data);
          this.shareFileService.addProgressValue(file.name, data);
          console.log(data);
          if (this.drawings.files.filter(item => { return (item.title === file.name) }).length === 0) {
            this.drawings.files.push({ title: file.name, url: '', version: '1', createDate: '', modifiedDate: '', device: new FileDevice(), loading: data })
          }
          else {
            this.drawings.files.filter(item => { return (item.title === file.name) })[0].loading = data;
          }
        } else if (data.status && data.status != 'Success') {
          this.c3Services.getPssDocument().subscribe(data => {
            this.drawings = data;
            this.drawings.files.forEach(item => {
              item.device = new FileDevice();
            })
          });
        }
        else {
          this.inProgress = false;
          this.drawings.files.filter(item => { return (item.title === file.name) })[0].url = data.c3Location;
          // this.message = `
          // ${this.message} <br>
          // <a style = 'text-decoration: none;'
          // href='${data.c3Location}' target='_self'>${data.c3Location.split('/').reverse()[0]}</a>
          // `;
          this.shareFileService.uploadComplete(file.name, data.c3Location);
        }
      });
    });
  }

  scanDocument() {
    //this.drawings.files.push();
    this.c3Services.getProposal().subscribe(filedata => {
      const file = new File([filedata], "Proposal" + Date.now().toString() + ".png", { lastModified: 1534584790000 });;
      this.c3Services.uploadFile(`Global/PSS Documents`, file, null, 'comment').subscribe(data => {
        // console.log('data inside filechange', data);
        // console.log('file inside filechange', file);
        if (typeof data === 'number') {
          this.inProgress = true;
          // console.log(data);
          this.shareFileService.addProgressValue(file.name, data);
          console.log(data);
          if (this.drawings.files.filter(item => { return (item.title === file.name) }).length === 0) {
            this.drawings.files.push({
              title: file.name, url: '', version: '1', createDate: '', modifiedDate: '', device: {
                mv_breaker: 3,
                mv_fuses: 2,
                mv_trans: 1,
                mv_motor: 0,
                mv_cabs: 0,
                lv_sw_fuse: 0,
                lv_adj_brk: 0,
                lv_non_adj_brk: 0,
                lv_trans: 0,
                lv_pan_100: 0,
                lv_pan_50: 0,
                lv_cp: 0,
                genset: 0
              }, loading: data
            })
            this.updateTotal();
          }
          else {
            this.drawings.files.filter(item => { return (item.title === file.name) })[0].loading = data;
          }
        } else if (data.status && data.status != 'Success') {
          this.c3Services.getPssDocument().subscribe(data => {
            this.drawings = data;
            this.drawings.files.forEach(item => {
              item.device = new FileDevice();
            })
          });
        }
        else {
          this.inProgress = false;
          this.drawings.files.filter(item => { return (item.title === file.name) })[0].url = data.c3Location;
          // this.message = `
          // ${this.message} <br>
          // <a style = 'text-decoration: none;'
          // href='${data.c3Location}' target='_self'>${data.c3Location.split('/').reverse()[0]}</a>
          // `;
          this.shareFileService.uploadComplete(file.name, data.c3Location);
        }
      });
    })

  }

}
