import { Url } from "url";

export class FileFolder {
    folders: FolderData[];
    foldersCount: number;
    files: FileData[];
    filesCount: number;
    folderId?: number;
}

export class FolderData {
    folderId: string;
    name: string;
    parentFolderId: string;
    createDate: string;
    modifiedDate: string;
    sName: string;
    type: string;
    currentId?: string;
    position?: number;
    configID?: string;
    mat?: string;
}

export class FileData {
    title: string;
    url: Url | string;
    version: string;
    fileEntryId?: string;
    folderId?: string;
    createDate: string;
    modifiedDate: string;
    sName?: string;
    type?: string;
    typeName?: string;
    status?: string;
    c3Location?: string;
    iconClass?: string;
    sendUrl?: string;
    versions?: Versions[];
    device?: FileDevice;
    loading?: number;
}

export class FileDevice {
  mv_breaker: number = 0;
  mv_fuses: number = 0;
  mv_trans: number = 0;
  mv_motor: number = 0;
  mv_cabs: number = 0;
  lv_sw_fuse: number = 0;
  lv_adj_brk: number = 0;
  lv_non_adj_brk: number = 0;
  lv_trans: number = 0;
  lv_pan_100: number = 0;
  lv_pan_50: number = 0;
  lv_cp: number = 0;
  genset: number = 0;
}

export class FileFolderTable {
  name?: string;
  parentFolderId?: string;
  title?: string;
  url?: Url | string;
  version?: string;
  fileEntryId?: string;
  folderId?: string;
  createDate?: string;
  modifiedDate?: string;
  sName?: string;
  type?: string;
  typeName?: string;
  status?: string;
  c3Location?: string;
  checked?: boolean;
  iconClass?: string;
}

class Versions {
  title: string;
  url: string;
  version: string;
  appUrl: Url;
}
