import { environment } from '../../environments/environment';
export class MiscStatic {
  public static get rootFolderId(): string {
    if (environment.envName === 'prod') {
      return '253107';
    } else {
      return '127163541';
    }
  }

  public static get rootAppName(): string {
    if (environment.envName === 'prod') {
      return 'Compas_SQR';
    } else {
      return 'Compas_DCC';
    }
  }

  public static get rootPathName(): string {
    if (environment.envName === 'prod') {
      return 'Compas_SQR';
    } else {
      return 'COMPAS_DCC';
    }
  }
}
