import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { StaticDataService } from '../_services/static-data.service';

@Injectable()
export class AppLoadService {

  private settings: { max_upload_limit: number };
  constructor( private httpClient: HttpClient,  private staticDataService: StaticDataService) { }

  getSettings() {
    const promise = this.httpClient.get<{ max_upload_limit: number, max_upload_size: number }>('./assets/json/misc-settings.json')
      .toPromise()
      .then(settings => {
        return settings;
      });

    return promise;
  }
}
