import { Injectable } from '@angular/core';
import { FileTypes as fileTypes, IconFonts as iconClass, Viewers as viewer, AppName as app, IconFiles as iconFile } from '../_constants';
import { Url } from 'url';

@Injectable()
export class FileManageService {

  constructor() { }

  getFileIcon(fileNameUrl: string) {
    // return font awesome icon class based on file
    let fileIcon = iconClass.commonFile;
    const fileName = fileNameUrl.toString().split('/').reverse()[0];
    const extension = fileName.includes('|a') ?
    fileName.substring(0, fileName.indexOf('|a')).split('.').reverse()[0] : fileName.split('.').reverse()[0];
    fileTypes.typeExtensions.forEach( file => {
      if ( file.app === app.word && file.extensions.includes(extension.toUpperCase())) {
        fileIcon = iconClass.word;
      } else if ( file.app === app.excel && file.extensions.includes(extension.toUpperCase()) ) {
        fileIcon = iconClass.excel;
      } else if ( file.app === app.pdf && file.extensions.includes(extension.toUpperCase())) {
        fileIcon = iconClass.pdf;
      } else if ( file.app === app.text && file.extensions.includes(extension.toUpperCase())) {
        fileIcon = iconClass.text;
      }
    });
    return fileIcon;
  }

  getFileIconPath( fileNameUrl: string ) {
    const fileName = fileNameUrl.toString().split('/').reverse()[0];
    const extension = fileName.split('.').reverse()[0];
    let fileIcon: string;
    fileIcon = iconFile.general;
    fileTypes.typeExtensions.forEach( file => {
      if ( file.app === app.fbx && file.extensions.includes(extension.toUpperCase())) {
        fileIcon = iconFile.fbx;
      } else if ( file.app === app.dae && file.extensions.includes(extension.toUpperCase()) ) {
        fileIcon = iconFile.dae;
      } else if ( file.app === app.obj && file.extensions.includes(extension.toUpperCase()) ) {
        fileIcon = iconFile.dae;
      } else if ( file.app === app.sharecad && file.extensions.includes(extension.toUpperCase())) {
        fileIcon = iconFile.sharecad;
      }
    });

    // to be optimized
    if ( extension.toUpperCase() === 'DWG' ) {
      fileIcon = 'assets/icons/323786-200.png';
    } else if ( extension.toUpperCase() === 'DXF' ) {
      fileIcon = 'assets/icons/dxf-512.png';
    }
    return fileIcon;
  }

  getViewer( url: string ) {
    const fileName = url.toString().split('/').reverse()[0];
    const extension = fileName.split('.').reverse()[0];
    let viewertype: string;
    fileTypes.typeExtensions.forEach( file => {
      if (file.app === app.sharecad && file.extensions.includes(extension.toUpperCase()) ) {
        viewertype = viewer.sharecad;
        // viewertype = '';
      } else if ( file.app === app.fbx && file.extensions.includes(extension.toUpperCase()) ) {
        viewertype = viewer.fbx;
      } else if ( file.app === app.dae && file.extensions.includes(extension.toUpperCase()) ) {
        viewertype = viewer.fbx;
      } else if ( file.app === app.obj && file.extensions.includes(extension.toUpperCase()) ) {
        viewertype = viewer.fbx;
      }
    });
    return viewertype;
  }

  getFileType(url: string) {
    const fileName = url.toString().split('/').reverse()[0];
    const extension = fileName.split('.').reverse()[0];
    let typeName = `${extension.toUpperCase()} File`;
    fileTypes.typeExtensions.forEach( file => {
      if (file.extensions.includes(extension.toUpperCase())) {
        typeName = file.typeName;
      }
    });
    return typeName;
  }

}
