@Component({
  selector: 'app-add-from-compas-drive-dialog',
  templateUrl: './add-from-compas-drive-dialog.component.html',
  styleUrls: ['./add-from-compas-drive-dialog.component.scss']
})
export class AddFromCompasDriveDialogComponent implements OnInit {

  public tableData: FileFolderTable[];
  public folderFiltered: FileFolderTable[];
  public fileFiltered: FileFolderTable[];
  public breadCrumb: FolderData[];
  public checkAll: boolean;
  public PMOStaticList: FileFolder;
  public qtNumber: string;
  private currentFolder: FolderData;
  public rootFolderId: string;
  private NotAllowedFolders: string[];
  public filterData: string;
  public loading: boolean;

  constructor(
    private driveService: C3Service,
    public dialog: MatDialog,
    private userService: UserService,
    public snackBar: MatSnackBar,
    private staticData: StaticDataService,
    private fileManage: FileManageService,
    private dialogRef: MatDialogRef<AddFromCompasDriveDialogComponent>,
  ) {
    // this.rootFolderId = genStatic.rootFolderId;
    // alert('rootFloderId' + this.rootFolderId);
   // this.staticData.callForEnvironmentData();
    this.tableData = [];
    this.breadCrumb = [];
    this.NotAllowedFolders = [];
    this.folderFiltered = [];
    this.fileFiltered = [];
    this.filterData = '';
    this.loading = false;
  }

  ngOnInit() {
    this.staticData.callForEnvironmentData().then(data=>{
      this.staticData.getEnvironmentSettings().subscribe( envData => {
        if ( envData.environment === 'production' ) {
          this.rootFolderId = envData.production.rootFolderId;
        } else {
          this.rootFolderId = envData.quality.rootFolderId;
        }
        // alert('rootFolderId' + this.rootFolderId);
      });
    });


    this.currentFolder = {
      name: this.qtNumber,
      createDate: '',
      modifiedDate: '',
      parentFolderId: this.rootFolderId,
      folderId: '0',
      sName: '',
      type: ''
    };
    this.breadCrumb = [];
    this.qtNumber = this.driveService.getSharedData();
    this.documentList();
  }


  initialService() {
    // User search service for getting role and id
    this.loading = true;
    this.userService.checkRole().subscribe(user => {
      if (user.userDetails[0]) {
        this.staticData.getStaticPermissionData().subscribe(permission => {
          let [pmo, qrcode, revit, map, global] = [0, 0, 0, 0, 0];
          user.userDetails[0].roleIdList.forEach(role => {
            if (permission.allowedUerRole.pmo.includes(role.roleId)) {
              pmo = 1;
            } else if (permission.allowedUerRole.qrcode.includes(role.roleId)) {
              qrcode = 1;
            } else if (permission.allowedUerRole.revit.includes(role.roleId)) {
              revit = 1;
            } else if (permission.allowedUerRole.map.includes(role.roleId)) {
              map = 1;
            } else if (permission.allowedUerRole.global.includes(role.roleId)) {
              global = 1;
            }
          });
          if (permission.allowedUserId.pmo.includes(user.userDetails[0].userId)) {
            pmo = 1;
          }
          if (permission.allowedUserId.qrcode.includes(user.userDetails[0].userId)) {
            qrcode = 1;
          }
          if (permission.allowedUserId.revit.includes(user.userDetails[0].userId)) {
            revit = 1;
          }
          if (permission.allowedUserId.map.includes(user.userDetails[0].userId)) {
            map = 1;
          }
          if (permission.allowedUserId.global.includes(user.userDetails[0].userId)) {
            global = 1;
          }
          this.NotAllowedFolders = [];
          if (pmo === 0) {
            this.NotAllowedFolders.push('pmo');
            this.NotAllowedFolders.push('PMO');
          }

          if (qrcode === 0) {
            this.NotAllowedFolders.push('qrcode');
          }

          if (revit === 0) {
            this.NotAllowedFolders.push('revit');
          }

          if (global === 0) {
            this.NotAllowedFolders.push('global');
          }

          if (map === 0) {
            this.NotAllowedFolders.push('map');
            this.NotAllowedFolders.push('MAP');
            this.NotAllowedFolders.push('Map');
          }
          this.getInitialList();
        });
      } else {
        this.snackBar.open('User not found', 'Close', {
          duration: 2000,
        });
        this.loading = false;
        // this.dialogRef.close();
      }
    }, error => {
      this.loading = false;
      this.snackBar.open('Not able to connect to Compas Drive. Please try again later or contact Compas Customer support', 'Close', {
        duration: 2000,
      });
    });
  }

  updateTable() {
    this.tableData = [...this.PMOStaticList.folders, ...this.PMOStaticList.files];
  }

  getInitialList() {
    this.driveService.getHomeList(this.qtNumber, this.NotAllowedFolders).subscribe(data => {
      if (data.filesCount <= 0 && data.foldersCount <= 0) {
        this.snackBar.open('No documents available in COMPAS drive', 'Close', {
          duration: 2000,
        });
        this.loading = false;
        // this.dialogRef.close();
      } else {
        this.PMOStaticList = data;
        let QtFolder: FolderData;
        if(this.qtNumber != null)
        {
          QtFolder = {
            folderId: data.folderId.toString(),
            name: this.qtNumber,
            parentFolderId: this.rootFolderId,
            createDate: '',
            modifiedDate: '',
            sName: '',
            type: ''
          };
          if (this.breadCrumb.length === 0) {
            const bCrumb = QtFolder;
            this.breadCrumb.push(bCrumb);
            this.currentFolder = QtFolder;
          }
        }

        this.staticData.getHomeStatic(this.NotAllowedFolders).subscribe(item => {
          if (data.foldersCount > 0) {
            const nameArray: string[] = data.folders.reduce((acc, curr) => {
              acc.push(curr.name);
              return acc;
            }, []);
            item.forEach(el => {
              if (!nameArray.includes(el.name)) {
                this.PMOStaticList.folders.push(el);
              } else if (el.type === itemType.global) {
                this.PMOStaticList.folders.push(el);
              } else if (el.type === itemType.qrcode) {
                this.PMOStaticList.folders.push(el);
              } else if (el.type === itemType.revit) {
                this.PMOStaticList.folders.push(el);
              }
            });
          } else {
            this.PMOStaticList.foldersCount = 3;
            this.PMOStaticList.folders = item;
          }
          this.updateTable();
        });
        const path = this.getCurrentPath();
        if (this.PMOStaticList.files.length > 0) {
          this.PMOStaticList.files.map(file => {
            file.typeName = this.fileManage.getFileType(file.url.toString());
            file.iconClass = this.fileManage.getFileIcon(file.url.toString());

            file.type = itemType.file;
          });
        }
        // file.url = this.filterUrlService.createEditUrl(file.url, path);
        const folders = this.PMOStaticList.folders;
        const breadCrumb = this.breadCrumb;
        this.updateTable();
        this.loading = false;
      }
    },
    //  error => {
    //   this.loading = false;
    //   this.snackBar.open('Not able to connect to Compas Drive. Please try again later or contact Compas Customer support', 'Close', {
    //     duration: 2000,
    //   });
    // }
    error => {
      if (
        error.error.message.includes('Could not find any folder with name')
      ) {
        this.createFolderWithQuoteNumber();
      } else {
        this.loading = false;
        this.snackBar.open('Not able to connect to Compas Drive. Please try again later or contact Compas Customer support', 'Close', {
        duration: 2000,
      });
      }
    }
    );
  }
  createFolderWithQuoteNumber() {
    this.driveService
      .createFolder(this.qtNumber, this.rootFolderId).subscribe(() => {
        this.getInitialList();
      });
  }
  getCurrentPath() {
    // cunstruct current folder path from breadcrumb
    let path = '';
    if (this.breadCrumb.length >= 2 && this.breadCrumb[1].type === itemType.global) {
      this.breadCrumb.forEach((link, index) => {
        if (index === 1) {
          path = link.name;
        } else if (index > 1) {
          path = path + '/' + link.name;
        }
      });
    } else {
      this.breadCrumb.forEach((link, index) => {
        index === 0 ? path = link.name : path = path + '/' + link.name;
      });
    }
    return path;
  }

  onSelectionChange(folder: FolderData) {
    this.loading = true;
    this.PMOStaticList.folders = [];
    this.PMOStaticList.files = [];
    this.currentFolder = folder;
    this.currentFolder.currentId = folder.name + folder.folderId;
    this.breadCrumb.push(folder);
    this.currentFolder.position = this.breadCrumb.length - 1;
    if (this.currentFolder.type === itemType.global) {
      this.driveService.getHomeList('Global', this.NotAllowedFolders).subscribe(data => {
        this.PMOStaticList = data;
        const path = this.getCurrentPath();
        if (this.PMOStaticList.files.length > 0) {
          this.PMOStaticList.files.map(file => {
            file.type = itemType.file;
            file.sendUrl = file.url.toString();
            file.typeName = this.fileManage.getFileType(file.url.toString());
            file.iconClass = this.fileManage.getFileIcon(file.url.toString());
          });
        }
        // file.url = this.filterUrlService.createEditUrl(file.url, path);
        this.updateTable();
        this.loading = false;
      });
    } else if (this.currentFolder.type === itemType.pmo) {
      this.loadSecondaryList(this.currentFolder.folderId, itemType.pmo);
    } else if (folder.name === this.qtNumber) {
      const breadCrumb = this.breadCrumb;
      this.initialService();
      // this.getInitialList();
    } else {
      const breadCrumb = this.breadCrumb;
      this.loadSecondaryList(folder.folderId, null);
    }
    this.updateTable();
  }

  loadSecondaryList(parentId: string, parentFolderType: string | null) {
    if (parentFolderType === itemType.pmo) {
      this.staticData.getPmoStatic().subscribe(item => {
        this.PMOStaticList.folders = item;
        const folders = this.PMOStaticList.folders;
        const breadCrumb = this.breadCrumb;
        this.updateTable();
        this.loading = false;
      });
    } else if (parentId !== '-1') {
      this.driveService.getChilds(parentId).subscribe(data => {
        this.PMOStaticList = data;
        if (this.PMOStaticList.filesCount > 0) {
          const path = this.getCurrentPath();
          // fileData.fileEntryId = this.fileManage.getFileType(fileData.url.toString()),
          this.PMOStaticList.files.map(fileData => {
            fileData.sendUrl = fileData.url.toString();
            fileData.typeName = this.fileManage.getFileType(fileData.url.toString()),
              fileData.iconClass = this.fileManage.getFileIcon(fileData.url.toString());
            fileData.type = itemType.file;
          });
        } else {
          this.PMOStaticList.files = [];
        }
        // fileData.url = this.filterUrlService.createEditUrl(fileData.url, path);

        if (this.currentFolder.name.toUpperCase() === 'PMO') {
          this.staticData.getPmoStatic().subscribe(item => {
            const nameArray: string[] = data.folders.reduce((acc, curr) => {
              acc.push(curr.name);
              return acc;
            }, []);
            item.forEach(pmo => {
              if (!nameArray.includes(pmo.name)) {
                this.PMOStaticList.folders.push(pmo);
              }
            });
            const folders = this.PMOStaticList.folders;
            const breadCrumb = this.breadCrumb;
            this.updateTable();
          });
        }
        if (this.PMOStaticList.folders.length > 0) {
          const folders = this.PMOStaticList.folders;
          const breadCrumb = this.breadCrumb;
        }
        this.updateTable();
        this.loading = false;
      });
    } else {
      this.loading = false;
    }
  }

  gotoBreadCrumb(index: number) {
    this.breadCrumb.length = index + 1;
  }

  addSelected() {
    this.dialogRef.close(this.tableData);
  }

  filterSearch(key: string) {
    if (key === '') {
      this.folderFiltered = [];
      this.fileFiltered = [];
      return false;
    }

    this.folderFiltered = this.tableData.filter(data => {
      return (
        data.type !== 'file' && data.name.toUpperCase().includes(key.toUpperCase())
      );
    });

    this.fileFiltered = this.tableData.filter(data => {
      return (
        data.type === 'file' && data.title.toUpperCase().includes(key.toUpperCase())
      );
    });
  }

  onChecked($event: any) {
    console.log($event);
  }

  myDrive() {
    // this.breadCrumb = [];
    // this.data = JSON.parse(this.messageData.getsharedData());
    // const user = this.data.userInfo.userID;
    // // const user = localStorage.getItem('userId');
    // this.driveService.getHomeList(`Global/${user}`, this.NotAllowedFolders).subscribe(data => {
    //   this.PMOStaticList = data;
    //   const path = this.getCurrentPath();
    //   if (this.PMOStaticList.files.length > 0) {
    //     this.PMOStaticList.files.map(file => {
    //       file.type = itemType.file;
    //       file.sendUrl = file.url.toString();
    //       file.typeName = this.fileManage.getFileType(file.url.toString());
    //       file.iconClass = this.fileManage.getFileIcon(file.url.toString());
    //     });
    //   }
    //   // file.url = this.filterUrlService.createEditUrl(file.url, path);
    //   this.updateTable();
    //   this.loading = false;
    // }, error => {
    //   this.loading = false;
    //   this.snackBar.open('Not able to connect to Compas Drive. Please try again later or contact Compas Customer support', 'Close', {
    //     duration: 2000,
    //   });
    // });
  }
  documentList()
  {
    this.breadCrumb = [];
    this.driveService.getPssDocument().subscribe(data => {
      this.PMOStaticList = data;
      if (this.PMOStaticList.files.length > 0) {
            this.PMOStaticList.files.map(file => {
              file.type = itemType.file;
              file.sendUrl = file.url.toString();
              file.typeName = this.fileManage.getFileType(file.url.toString());
              file.iconClass = this.fileManage.getFileIcon(file.url.toString());
            });
          }
          // file.url = this.filterUrlService.createEditUrl(file.url, path);
          this.updateTable();
          this.loading = false;
        }, error => {
          this.loading = false;
          this.snackBar.open('Not able to connect to Compas Drive. Please try again later or contact Compas Customer support', 'Close', {
            duration: 2000,
          });
        });
  }
}
