import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { FolderData, UserPermission, StaticEnv } from '../_models';
import { environment } from '../../environments/environment';
// import { UserService } from './user.service';
import { map } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';

@Injectable()
export class StaticDataService {
  private pmoStaticUrl = '../../assets/json/pmo-static.json';
  private mapStaticUrl = '../../assets/json/map-static.json';
  private homeStaticUrl = '../../assets/json/home-static.json';
  private restrictedNamesUrl = '../../assets/json/restricted-names.json';
  private lineItemStaticUrl = '../../assets/json/line-item-static.json';
  private userRoleStaticUrl = '../../assets/json/user-role.json';
  private envSettingsUrl = '../../assets/json/environment.json';
  // this env variable will change only in production environment
  private envName: BehaviorSubject<StaticEnv>;

  constructor( private http: HttpClient) {

   }

  getPmoStatic() {
    return this.http.get<FolderData[]>(this.pmoStaticUrl);
  }

  getMapStatic() {
    return this.http.get<FolderData[]>(this.mapStaticUrl);
  }

  getHomeStatic(NotAllowedFolders: string[]) {
    let headers = new HttpHeaders();
    headers = headers.set('errorMessage', 'false').set('loading', 'false');
    return this.http.get<FolderData[]>(this.homeStaticUrl, { headers }).pipe(
      map( list => {
        return list.filter( item => {
          if (!NotAllowedFolders.includes(item.type)) {
            return item;
          }
        });
      })
    );
  }

  getRestrictedNames() {
    return this.http.get<string[]>(this.restrictedNamesUrl);
  }

  getLineItemStatic() {
    return this.http.get<FolderData[]>(this.lineItemStaticUrl);
  }

  getStaticPermissionData() {
    let headers = new HttpHeaders();
    headers = headers.set('errorMessage', 'false').set('loading', 'false');
    return this.http.get<UserPermission>(this.userRoleStaticUrl, { headers });
  }

  callForEnvironmentData() {
    let headers = new HttpHeaders();
    headers = headers.set('errorMessage', 'false').set('loading', 'false');
    const promise = this.http.get<StaticEnv>(this.envSettingsUrl, { headers })
    .toPromise()
    .then(settings => {
      this.envName = new BehaviorSubject(settings);
      return settings;
    });
    return promise;
  }

  getEnvironmentSettings() {
   // alert('env' + this.envName);
      return this.envName;
  }
}
