import { NgModule, APP_INITIALIZER } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AppLoadService } from './app-load.service';
import { StaticDataService } from '../_services/static-data.service';

export function init_app(staticDataService: StaticDataService, ) {
  return () => staticDataService.callForEnvironmentData();
}

export function get_settings(appLoadService: AppLoadService) {
  return () => appLoadService.getSettings();
}
@NgModule({
  imports: [
    CommonModule
  ],
  declarations: [],
  providers: [
    AppLoadService,
    StaticDataService,
    { provide: APP_INITIALIZER, useFactory: init_app, deps: [StaticDataService], multi: true },
    { provide: APP_INITIALIZER, useFactory: get_settings, deps: [AppLoadService], multi: true }
  ]
})
export class AppLoadModule { }
