  sort(name: number, asc: boolean) {
    /* sort the entire list
    argument:
    name: index of clumn as number
    asc: order(asc/desc) as boolean
    return : sorted array
    */
    this.fileFolderTree.sort((a, b) => {
      let nameA: string | number;
      let nameB: string | number;
      const returnValue = 0;
      switch (name) {
        case 0: {
          nameA = a.name ? a.name.toUpperCase() : a.title.toUpperCase();
          nameB = b.name ? b.name.toUpperCase() : b.title.toUpperCase();
          break;
        }
        case 1: {
          nameA = a.modifiedDate;
          nameB = b.modifiedDate;
          break;
        }
        case 2: {
          nameA = a.typeName !== undefined ? a.typeName.toUpperCase() : itemTypes.folder.toUpperCase();
          nameB = b.typeName !== undefined ? b.typeName.toUpperCase() : itemTypes.folder.toUpperCase();
          break;
        }
      }
      if (asc) {
        if (nameA < nameB) { return -1; }
        if (nameA > nameB) { return 1; }
      } else {
        if (nameA > nameB) { return -1; }
        if (nameA < nameB) { return 1; }
      }
      return returnValue;
    });
  }
