import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { FileFolderTree } from '../_models';
import { TreeviewItem } from 'ngx-treeview';

@Injectable()
export class ShareFileService {

  private selectedFiles: string[];
  public treeSubject = new Subject<FileFolderTree>();
  public treeState = this.treeSubject.asObservable();
  filFolderTree: TreeviewItem[];
  constructor() {
    this.filFolderTree = [];
   }

  getFileShare() {
    return this.treeState;
  }

  addFile(file: TreeviewItem, value = 0) {
    this.filFolderTree.push(
      new TreeviewItem({
        text: file.text, value: value
      })
    );
    this.treeSubject.next({ updated: true, items: this.filFolderTree });
  }

  removeFromTree(index: number) {
    console.log(this.filFolderTree);
    console.log(index);
  }

  addProgressValue( fileUrl: string, value: number ) {
    this.filFolderTree.map( item => {
      if (item.text === fileUrl) {
        item.value = value;
      }
    });
    this.treeSubject.next({ updated: true, items: this.filFolderTree });
  }

  uploadComplete(fileUrl: string, c3Location: string) {
    this.filFolderTree.map( item => {
      if (item.text === fileUrl) {
        item.text = c3Location;
      }
    });
    this.treeSubject.next({ updated: true, items: this.filFolderTree });
  }

  cleanFiles() {
    this.filFolderTree = [];
    this.treeSubject.next({ updated: true, items: this.filFolderTree });
  }
}
