import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { UserDetails } from '../_models';

@Injectable()
export class UserService {
  private searchUserId: string;
  private searchUrl = environment.url + '/JobServer/webresources/UserSearch';
  constructor( private http: HttpClient ) { }

  checkRole() {
    let headers = new HttpHeaders();
    headers = headers.set('errorMessage', 'false').set('loading', 'false');
    // this.searchUserId = localStorage.getItem('userId');
    this.searchUserId = 'krisnx';
    const payload = {
      applicationType: '200',
      userSearch: {
        searchUserId: this.searchUserId
      }
    };

    return this.http.post<UserDetails>(this.searchUrl, payload, { headers });
  }
}
