import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpRequest, HttpEvent, HttpEventType } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { FileFolder, FileVersionsRespnse, FileData, NewFolder } from '../_models';
import { StaticDataService } from './static-data.service';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class C3Service {

  private c3ServicesFolderFiles = environment.url + 'c3services/ws/services/folder/files';
  private c3ServicesFolderFilesTest = 'assets/json/static-files-test.json';
  private c3ServicesFileVersions = environment.url + 'c3services/ws/services/d2';
  private uploadUrl = environment.url + 'c3services/ws/services/u2';
  private newFolderUrl = environment.url + 'c3services/ws/services/add/folder';
  private deleteFolderUrl = environment.url + 'c3services/ws/services/delete/folder';
  private deleteFileEntryUrl = environment.url + 'c3services/ws/services/delete/file-entry';
  private qtInfo: { iD: string };
  rootPathName: any;
  rootFolderId: any;
  rootAppName: any;
  constructor(private http: HttpClient, private staticService: StaticDataService) {
    this.qtInfo = JSON.parse(window.frames.sessionStorage.getItem('QUOTEINFO'));
  }

  ngOnInit() {
    this.envSettings();
  }

  envSettings() {
    this.staticService.getEnvironmentSettings().subscribe(data => {
      if (data.environment === 'production') {
        this.rootFolderId = data.production.rootFolderId;
        this.rootAppName = data.production.rootAppName;
        this.rootPathName = data.production.rootPathName;
      } else {
        this.rootFolderId = data.quality.rootFolderId;
        this.rootAppName = data.quality.rootAppName;
        this.rootPathName = data.quality.rootPathName;
      }
    });
  }

  getPssDocument() {
    this.envSettings();
    const formData = {
      'applicationType': '200',
      'attributeRequired': false
    }
      formData['parentFolder'] = `${this.rootPathName}/Global/PSS Documents`;
      formData['path'] = this.rootPathName ;

      const httpOptions = {
        headers: new HttpHeaders({
          errorMessage: 'false',
          'Content-Type': 'application/json',
          'c3': 'true'
        })
      };
      return this.http.post<FileFolder>(this.c3ServicesFolderFiles, formData, httpOptions);

  }
  getHomeList(fileName: string | null, NotAllowedFolders: string[]) {
    this.envSettings();
    const formData = {
      'applicationType': '200',
      'attributeRequired': false
    }
    if (fileName) {
      formData['parentFolder'] = `${this.rootPathName}/${fileName}`;
      formData['path'] = `${this.rootPathName}/${fileName}`;
    } else {
      formData['parentFolder'] = `${this.rootPathName}`;
      formData['path'] = this.rootPathName;
    }

    const httpOptions = {
      headers: new HttpHeaders({
        errorMessage: 'false',
        'Content-Type': 'application/json',
        'c3': 'true'
      })
    };
    return this.http.post<FileFolder>(this.c3ServicesFolderFiles, formData, httpOptions).pipe(
      map(list => {
        if (NotAllowedFolders.length !== 0) {
          list.folders = list.folders.filter(folder => {
            if (!NotAllowedFolders.includes(folder.name)) {
              return folder;
            }
          });
        }
        return list;
      })
    );

    // return this.http.get<FileFolder>(this.c3ServicesFolderFilesTest);
  }

  getChilds(parentId: string) {
    const formData = {
      'applicationType': '200',
      'folderId': parentId,
      'attributeRequired': false
    };
    const httpOptions = {
      headers: new HttpHeaders({
        'c3': 'true'
      })
    }
    return this.http.post<FileFolder>(this.c3ServicesFolderFiles, formData, httpOptions);
  }

  getFileVersions(title: string, path: string) {
    let payload: any;
    const intiFolder = path.split('/')[0];
    const pathArray = path.split('/');
    if (pathArray[1] === 'Global') {
      pathArray.forEach((item, index) => {
        if (index === 1) {
          path = item;
        } else if (index > 1) {
          path = path + '/' + item;
        }
      });
    }
    payload = {
      applicationType: '200',
      appName: `${this.rootAppName}`,
      title,
      path: `${this.rootPathName}/${path}`
    };
    const httpHeaders = new HttpHeaders({
      'Content-Type': 'application/json',
      'c3':'true'
    });
    return this.http.post<FileVersionsRespnse>(this.c3ServicesFileVersions, payload, { headers: httpHeaders });
  }

  uploadFile(folderPath: string, file: File, fileType: string | null, comment: string) {
    this.envSettings();
    let payload = '';
    const intiFolder = folderPath.split('/').reverse()[0];
    payload = `{
      'destinationLocation': '${this.rootPathName}/${folderPath}',
      'appName': '${this.rootAppName}',
      'description': 'uploaded from compas-drive',
      'comment': '${comment}'
    }`;

    if (!fileType) {
      if (intiFolder === 'Global') {
        payload = `{
          'destinationLocation': '${this.rootPathName}/${intiFolder}',
          'appName': '${this.rootAppName}',
          'description': 'uploaded from Compas-drive to global',
          'comment': '${comment}'
        }`;
      } else {
        if (folderPath.includes('/Global/')) {
          const Folders: string[] = folderPath.split('/');
          folderPath = '';
          for (let index = 1; index < Folders.length; index++) {
            folderPath = folderPath + '/' + Folders[index];
          }
          payload = `{
            'destinationLocation': '${this.rootPathName}/${folderPath}',
            'appName': '${this.rootAppName}',
            'description': 'uploaded from compas-drive to folderpath',
            'comment': '${comment}'
          }`;
        } else {
          payload = `{
            'destinationLocation': '${this.rootPathName}/${folderPath}',
            'appName': '${this.rootAppName}',
            'description': 'uploaded from compas-drive to folderpath',
            'comment': '${comment}'
          }`;
        }
      }
    } else {
      if (fileType === 'global') {
        payload = `{
          'destinationLocation': '${this.rootPathName}/Global',
          'appName': '${this.rootAppName}',
          'description': 'uploaded from compas-drive to folderpath',
          'comment': '${comment}'
        }`;
      } else {
        payload = `{
          'destinationLocation': '${this.rootPathName}/${folderPath}',
          'appName': '${this.rootAppName}',
          'description': 'uploaded from compas-drive to folderpath',
          'comment': '${comment}'
        }`;
      }
    }

    let headers = new HttpHeaders();
    headers = headers.append('c3', 'loading');
    const fd: FormData = new FormData();
    fd.append('file', file);
    fd.append('attributes', payload);

    const req = new HttpRequest('POST', this.uploadUrl, fd, {
      headers: headers,
      reportProgress: true
    });

    return this.http.request<FileData>(req).pipe(
      map(event => this.getEventMessage(event, file))
    );
  }

  private getEventMessage(event: HttpEvent<any>, file: File) {
    switch (event.type) {
      case HttpEventType.Sent:
        return 0;

      case HttpEventType.UploadProgress:
        const percentDone = Math.round(100 * event.loaded / event.total);
        return percentDone;

      case HttpEventType.Response:
        return event.body;

      default:
        return 100;
    }
  }

  getSharedData() {
    let quoteId = null;
    if (this.qtInfo) {
      quoteId = this.qtInfo.iD;
    }
    return quoteId;
  }

  createFolder(name: string, parentId: string) {
    const fd: FormData = new FormData();
    fd.append('folderName', name);
    fd.append('parentFolderId', parentId);
    const httpOptions = {
      headers: new HttpHeaders({
        'c3': 'true'
      })
    };
    return this.http.post<NewFolder>(this.newFolderUrl, fd, httpOptions);
  }

  deleteFolder(id: number[]) {
    const payload = {
      applicationType: 'CLOUD',
      appName: `${this.rootAppName}`,
      folderIds: id
    };
    const httpOptions = {
      headers: new HttpHeaders({
        'c3': 'true'
      })
    };
    return this.http.post(this.deleteFolderUrl, payload, httpOptions);
  }

  deleteFileEntry(id: number[]) {
    const payload = {
      applicationType: '200',
      appName: `${this.rootAppName}`,
      fileIds: id
    };
    const httpOptions = {
      headers: new HttpHeaders({
        'c3': 'true'
      })
    };
    return this.http.post(this.deleteFileEntryUrl, payload, httpOptions);
  }

  getProposal () {
    return this.http.get('assets/Documents/Proposal.jpg',  { responseType: 'blob'});
  }

}